<template>
  <div class="layout">
    <div class="layout-nav">
      <div class="navbar">
        <logo />
        <div class="nav">
          <router-link :class="active === 1? 'active':''" to="/">主頁</router-link>
          <router-link :class="active === 2? 'active':''" to="/news">公司及新闻</router-link>
          <router-link :class="active === 3? 'active':''" to="/portfolio">作品</router-link>
          <router-link :class="active === 4? 'active':''" to="/member">成員</router-link>
          <router-link :class="active === 5? 'active':''" to="/aboute">联系我们</router-link>
          <div class="clean"></div>
        </div>
      </div>
    </div>
    <div class="layout-content">
      <transition class='slide-ul' name="indexlist">
        <div>
          <slot></slot>
        </div>
      </transition>
    </div>
  </div>
</template>
<script>
  import logo from './logo.vue'
  export default {
    components:{
      logo
    },
    props:['active']
  }
</script>

<style lang="less" scoped>
  /*Vue transition*/
  .outLeftInRight-transition,
  .fadeOutLeft-transition, 
  .fadeInLeft-transition {
    transition: transform .3s ease-in-out;
  }
  /*进入时的状态，从右开始*/
  .outLeftInRight-enter,
  .fadeInLeft-enter,
  .fadeInLeft-leave{
    transform: translateX(100%);
  }
  /*离开时的状态，从左开始*/
  .outLeftInRight-leave,
  .fadeOutLeft-enter,
  .fadeOutLeft-leave{
    transform: translateX(-100%);
  }
  .navbar{
    position: absolute;
    top:-100px;
    bottom: 0;
    height: 200px;
    margin: auto;
    // float: left;
    // margin: auto 0;
    // display: inline-block;
    .nav{
      padding: 32px 0 0 0;
      a{
        float: left;
        border-top:2px solid #000;
        padding: 10px 6px 0 6px;
        margin-right: 10px;
        &.active,&:hover{
            border-top:2px solid #fff;
            text-decoration: none;
            transition: border 0.6s ease-in-out; 
        }
      }
    }
    a{
      writing-mode:tb-rl;
      line-height: 16px;
      display: inline-block;
      word-wrap: break-word; 
      letter-spacing: 8px; 
      width: 16px;
      font-size: 16px;
      color: #fff;

    }
  }
</style>



